[v-cloak]{
    display: none;
}

.expand-transition {
    transition: all .3s ease;
    height: auto;
    padding: 10px;
    overflow: hidden;
}

/* .expand-enter defines the starting state for entering */
/* .expand-leave defines the ending state for leaving */
.expand-enter, .expand-leave {
    height: 0;
    padding: 0 10px;
    opacity: 0;
}